/* ----- Skills comp ------------------- */

.skills-card {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  background-color: $cl-3;
  padding: 20px 10px;
  margin: 0 -10px 20px;
  overflow: hidden;
  &__info {
    flex-grow: 1; }
  &__icon {
    min-width: 72px;
    flex-shrink: 0;
    margin-right: 20px;
    float: left;
    margin-bottom: 10px;
    text-align: center; } }

.txt-md {
  color: $cl-8;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px; }

.txt-st {
  color: $cl-4;
  font-size: 12px;
  line-height: 14px; }

.skills-card__info {
  > * + * {
    margin-top: 10px; } }

.c-cards {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-right: -1%;
  margin-left: -1%; }

.c-card {
  min-width: 48%;
  max-width: 48%;
  margin-bottom: 5px;
  margin-right: 1%;
  margin-left: 1%;
  height: 150px;
  display: block;
  text-decoration: none;
  outline: none;
  overflow: hidden;
  border-radius: 10px;
  img {
    transition: all 0.2s linear;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center top; }

  &:hover, &:focus {
    img {
      transform: scale(1.1); } } }

.team-slider-wrapp {
  overflow: hidden;
  padding: 20px 0 100px;
  .team-slider, .slick-list, .slick-track {
    overflow: visible; } }


.t-card {
  border-radius: 10px;
  background-color: #f8fbfa;
  display: block;
  text-decoration: none;
  outline: none;
  position: relative;
  z-index: 1;
  width: 290px;
  margin: 0 40px 0 0;
  padding: 20px 10px;
  background: #f8fbfa linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, $cl-3 100%);
  box-shadow: 0px 2.76726px 2.21381px rgba(24, 53, 8, 0.0168687), 0px 6.6501px 5.32008px rgba(24, 53, 8, 0.0242336), 0px 12.5216px 10.0172px rgba(24, 53, 8, 0.03), 0px 22.3363px 17.869px rgba(24, 53, 8, 0.0357664), 0px 41.7776px 33.4221px rgba(24, 53, 8, 0.0431313), 0px 100px 80px rgba(24, 53, 8, 0.06);
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-align: right;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, $cl-3 100%);
      height: 50%; }
    img {
      max-height: 100%;
      max-width: 100%; } }

  &-exp {
    position: absolute;
    z-index: 3;
    right: -15px;
    bottom: 30px;
    background: url(../img/t-card-bg.png) right bottom no-repeat;
    background-size: contain;
    width: 130px;
    height: 130px;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: #fff;
    &__inner {
      position: relative;
      top: -3px;
      right: -4px;
      > span {
        color: $cl-3;
        display: block;
        font-size: 14px;
        line-height: 1;
        font-weight: 500; } } }

  .t-card-exp__cap {
    position: relative;
    line-height: 1; }

  .t-card-exp__value {
    color: $cl-3;
    font-family: $site-font-family-2;
    font-size: 30px;
    line-height: 1;
    font-weight: 400;
    text-transform: uppercase; }

  .t-card-exp__rotate {
    display: inline-block;
    transform: rotate(90deg);
    text-align: left;
    position: relative;
    top: -8px;
    color: $cl-3;
    font-size: 10px;
    font-weight: 600;
    line-height: 14px; }

  &-info {
    position: relative;
    z-index: 2;
    padding-right: 80px;
    min-height: 340px;
    display: flex;
    justify-content: space-between;
    flex-direction: column; } }

.t-card-info__txt-st {
  color: $cl-4;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: 5px; }

.t-card-info__name {
  color: $cl-6;
  font-size: 24px;
  line-height: 32px; }

.t-card-info__cap {
  margin-bottom: 40px; }

.t-card-info__tt {
  color: $cl-5;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  margin-bottom: 5px; }

.t-card-info__txt {
  color: $cl-8;
  font-size: 12px;
  line-height: 16px; }

.t-card-info__point {
  margin-bottom: 20px; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .c-card {
    height: 270px; }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .txt-md {
    font-size: 16px;
    line-height: 24px; }

  .txt-st {
    font-size: 14px;
    line-height: 18px; }

  .skills-cards {
    display: flex;
    justify-content: space-between;
    &__half-col {
      min-width: calc(50% - 14px);
      max-width: calc(50% - 14px); } }

  .skills-card {
    margin: 0 0 28px;
    padding: 20px;
    display: flex;
    justify-content: flex-start; }

  .skills-card__icon {
    float: none;
    flex-shrink: 0; }
  .c-card {
    min-width: 23%;
    max-width: 23%;
    height: 200px; }

  .t-card {
    padding: 35px 30px 15px;
    width: 400px; }

  .t-card-info__txt-st {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 10px; }

  .t-card-info__name {
    font-size: 26px;
    line-height: 32px; }

  .t-card-info__tt {
    color: $cl-5;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 5px; }

  .t-card-info__txt {
    color: $cl-8;
    font-size: 14px;
    line-height: 20px; }

  .t-card-info {
    min-height: 380px; }

  .slider-cap-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    .cap-section_left {
      text-align: left; }
    .team-slider-nav {
      flex-shrink: 0;
      margin-left: 100px;
      margin-bottom: 80px; } }

  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {

  .skills-cards {
    margin: 70px 0 20px; }

  .skills-card {
    padding: 34px 40px; }

  .skills-card__icon {
    margin-right: 30px; }
  .c-card {
    height: 264px; }

  .c-cards {
    margin-top: 70px; }

  .t-card-info__txt-st {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 7px; }

  .t-card-info__name {
    font-size: 30px;
    line-height: 40px; }

  .t-card-info__tt {
    font-size: 16px;
    line-height: 24px; }

  .t-card-info__txt {
    font-size: 16px;
    line-height: 24px; }

  .t-card {
    border-radius: 10px;
    width: 490px;
    margin-right: 130px;
    padding-left: 50px;
    padding-right: 77px;
    padding-bottom: 30px; }
  .t-card__img {
    border-radius: 10px; }
  .t-card-info {
    min-height: 420px;
    padding-right: 90px; }

  .t-card-info__point {
    margin-bottom: 30px; }

  .t-card-exp {
    width: 271px;
    height: 276px;
    right: -105px;
    bottom: -10px; }

  .t-card .t-card-exp__value {
    font-size: 62px; }

  .t-card .t-card-exp__rotate {
    font-size: 18px;
    top: -26px;
    right: -4px; }

  .t-card-exp__inner > span {
    font-size: 23px; }

  .team-slider-wrapp {
    padding-bottom: 150px; }
  /* end media min width 1000 pixels */ }


