/* ----- Inner pages ------------------- */
.box-step-cards {
  text-align: center;
  margin: 40px 0; }

.step-card {
  text-align: left;
  border-radius: 10px;
  border: 1px solid $cl-5;
  padding: 10px 10px 20px 20px;
  color: $cl-4;
  background-color: #fff;
  margin-bottom: 20px;
  &__cap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 15px;
    .title-site, h1, h2, h3, h4, h5, h6 {
      margin-bottom: 0;
      line-height: 30px;
      color: $cl-27;
      overflow: hidden;
      text-overflow: ellipsis; } }
  &__value {
    color: $cl-5;
    font-size: 40px;
    line-height: 1;
    font-weight: 100;
    flex-shrink: 0;
    margin-left: 10px; } }

.cap-section-grid {
  margin-bottom: 40px;
  &__right {
    text-align: center; }
  .cap-section {
    margin-bottom: 20px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .step-cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 40px 0;
    .step-card {
      min-width: 49%;
      max-width: 49%;
      margin-bottom: 15px; } }

  .box-step-cards {
    margin: 60px 0; }

  .cap-section-grid {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    &__right {
      text-align: right;
      padding-top: 5px;
      margin-left: 25px;
      flex-shrink: 0; }
    .cap-section {
      margin-bottom: 0;
      text-align: left; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .step-cards {
    margin: 60px 0;
    .step-card {
      min-width: 48.8%;
      max-width: 48.8%;
      margin-bottom: 30px; } }

  .step-card {
    margin-bottom: 30px;
    padding: 7px 19px 35px 45px;
    &__value {
      font-size: 72px; }
    &__txt {
      padding-right: 50px;
      font-size: 14px;
      line-height: 20px;
      > *+* {
        margin-top: 15px; } } }

  .step-card__cap .title-site, .step-card__cap h1, .step-card__cap h2, .step-card__cap h3, .step-card__cap h4, .step-card__cap h5, .step-card__cap h6 {
    line-height: 41px; }

  .box-step-cards {
    margin: 90px 0; }
  /* end media min width 1000 pixels */ }


/* ----- END Inner pages --------------- */
