/* ----- Studio page ------------------- */

.about-txt-box {
  > * + * {
    margin-top: 20px; }
  > div > * + * {
    margin-top: 15px; } }

.col-dark2 {
  color: $cl-6; }
.col-shadow {
  color: $cl-4; }

.txt-lg {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500; }

.about-icons-box {
  padding: 20px 15px;
  // background: url(../img/border2.jpg) center center no-repeat
  // background-size: 100% 100%
  border-radius: 50px;
  border: 1px dashed $cl-20;
  text-align: center;
  margin-top: 50px; }

.about-icons {
  margin-top: 50px; }
.about-icon {
  margin-bottom: 20px;
  &__icon {
    margin-bottom: 10px; }
  &__txt-tt {
    color: $cl-7;
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 5px; }
  &__txt-st {
    color: $cl-4;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px; } }

.box-container {
  padding: 40px 0;
  box-shadow: 0 3px 2px rgba(24, 53, 8, 0.02), 0 7px 5px rgba(24, 53, 8, 0.02), 0 13px 10px rgba(24, 53, 8, 0.03), 0 22px 18px rgba(24, 53, 8, 0.04), 0 42px 33px rgba(24, 53, 8, 0.04), 0 100px 80px rgba(24, 53, 8, 0.06);
  border-radius: 10px;
  background-color: $cl-19;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, $cl-3 100%); }

.t-card_one {
  background: none;
  box-shadow: none;
  width: 100%;
  margin: 0;
  .t-card-info {
    min-height: 370px; } }

.t-card__img {
  picture {
    width: 100%;
    height: 100%;
    display: block; } }

.box-container__item {
  .txt-sm {
    color: $cl-5;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px; } }

.team-one-slider-wrapp {
  padding-bottom: 80px; }

.card-project-list {
  &__item {
    padding: 10px 0; } }

.slider-review-sm-wrapp_second {
  .p-card {
    width: 290px; } }

.box-container {
  overflow: hidden; }

.box-container__item {
  margin-bottom: 40px;
  &:last-child {
    margin-bottom: 0; } }

.c-cards_sm.c-cards {
  margin-top: 20px;
  margin-bottom: 0; }

.card-project-author {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 10px -20px -10px;
  border-top: 1px solid #547266;
  padding: 10px 20px 0;
  &__pic {
    width: 35px;
    height: 36px;
    background-color: $cl-7;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: 15px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  &__txt {
    color: #a2c3b6;
    font-size: 12px;
    font-weight: 500;
    line-height: 20px; } }

.card-project_sm {
  height: 230px; }
.card-project_md {
  height: 265px; }
.card-project_lg {
  height: 300px; }
.card-project_second {
  margin-bottom: 10px;
  &:hover, &:focus {
    box-shadow: 0 12px 19px rgba(50, 78, 67, 0.1), 0 6px 11px rgba(50, 78, 67, 0.2); } }

.contact-box {
  padding: 40px;
  background: url(../img/border3.png) center center no-repeat;
  background-size: 100% 100%;
  text-align: center;
  &-adr {
    display: inline-block;
    text-align: left;

    > * + * {
      margin-top: 10px; } } }

/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .card-project-list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 20px -14px;
    &__item {
      padding: 14px 14px;
      min-width: 50%;
      max-width: 50%; } }

  .slider-review-sm-wrapp_second {
    .p-card {
      width: 490px; } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .txt-lg {
    font-size: 18px;
    line-height: 30px; }

  .about-txt-box {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
    &__left {
      max-width: 49%;
      min-width: 49%; }
    &__right {
      max-width: 49%;
      min-width: 49%; } }

  .about-icons-box {
    padding: 50px; }

  .about-icons {
    display: flex;
    justify-content: space-between; }


  .about-icon {
    &__icon {
      min-height: 73px;
      line-height: 72px; }
    &__txt-tt {
      font-size: 26px;
      line-height: 34px; }
    &__txt-st {
      font-size: 14px;
      line-height: 18px; } }

  .t-card_one {
    padding: 40px 60px;
    .t-card-info {
      padding-right: 120px; }
    .t-card-info__bottom {
      margin-bottom: 60px;
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      .t-card-info__point {
        width: 50%;
        padding-right: 20px; } } }

  .card-project-list__item_tabl-hidden {
    display: none; }

  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .txt-lg {
    font-size: 20px;
    line-height: 30px; }

  .about-txt-box {
    > * + * {
      margin-top: 50px; }
    > div > * + * {
      margin-top: 25px; }
    &_st {
      > * + * {
        margin-top: 0; } } }

  .about-icons-box {
    border: none;
    background: url(../img/border2.jpg) center center no-repeat;
    background-size: 100% 100%;
    border-radius: 10px;
    margin-top: 100px; }

  .about-icon {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &__icon {
      flex-shrink: 0;
      margin-right: 15px;
      margin-bottom: 0; }
    &__txt {
      text-align: left; }
    &__txt-tt {
      font-size: 28px;
      line-height: 36px; }
    &__txt-st {
      font-size: 14px;
      line-height: 20px; } }

  .team-one-slider-wrapp {
    padding: 0 70px;
    .slick-prev {
      left: -80px; }
    .slick-next {
      right: -80px; }
    .slick-prev, .slick-next {
      margin-top: -60px; }
    .slick-dots {
      bottom: 137px; } }
  .t-card_one {
    padding: 70px 20px;
    .t-card-exp {
      right: 25px;
      bottom: 65px; }
    .t-card__img:before {
      height: 70%; } }


  .t-card_one .t-card-info {
    min-height: 470px; }

  .t-card_one .t-card-info__bottom {
    margin-bottom: 190px; }
  .t-card_one .t-card-info__bottom .t-card-info__point {
    max-width: 265px; }

  .slider-review-sm-wrapp_second {
    .p-card {
      width: 630px;
      max-width: none; } }

  .slider-review-sm-wrapp_second {
    padding: 20px 0; }

  .c-cards_sm.c-cards {
    margin-top: 40px;
    margin-bottom: 60px; }

  .box-container__item {
    .txt-sm {
      padding: 0 90px; } }

  .contact-box {
    max-width: 440px;
    margin-left: auto; }
  /* end media min width 1000 pixels */ }

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .about-icon {
    &__txt-tt {
      font-size: 30px;
      line-height: 40px; }
    &__txt-st {
      font-size: 14px;
      line-height: 21px; } }

  .box-container {
    max-width: 1200px;
    margin: 0 auto; }

  .card-project-list {
    &__item {
      min-width: 33.3%;
      max-width: 33.3%; } }

  .card-project-list__item_tabl-hidden {
    display: block; }

  .slider-review-sm-wrapp_second {
    overflow: visible;
    .slider-review-sm {
      overflow: visible;
      .slick-track, .slick-slide, .slick-list {
        overflow: visible; } }
    .p-card {
      width: 870px;
      max-width: none;
      .p-card__info {
        padding-right: 120px; } } }

  .about-icon__icon {
    max-width: 60px;
    min-width: 60px; }
  /* end media min width 1200 pixels */ }


/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .team-one-slider-wrapp {
    .slick-prev {
      left: -120px; }
    .slick-next {
      right: -120px; } }
  /* end media min width 1350 pixels */ }


/* ----- END Studio page --------------- */
