/* ----- Index page -------------------- */

.banner-social {
  background: url(../img/bg-form.jpg) left center no-repeat;
  background-size: cover;
  padding: 40px 0;
  border-radius: 30px;
  color: #fff;
  .title-site, h1, h2, h3, h4, h5, h6 {
    color: #fff; }
  .form-group__field {
    background-color: $cl-3; } }

.cap-section_green {
  .title-site_h1, .title-site_h2, h1, h2 {
    color: #8ED3A9; } }

.dots-hidden {
  .slick-dots {
    display: none; } }

.slider-slide {
  .card-project {
    width: 250px; } }
.card-project {
  display: block;
  text-decoration: none;
  outline: none;
  position: relative;
  z-index: 1;
  border-radius: 10px;
  overflow: hidden;
  height: 300px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.card-project__open {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  background-image: linear-gradient(to bottom, rgba(54, 120, 81, 0) 0%, rgba(24, 53, 36, 0.9) 100%);
  color: rgba(255, 255, 255, 0.9);
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  padding: 20px; }

.card-project__name {
  color: rgba(255, 255, 255, 0.9);
  font-size: 22px;
  font-weight: 500;
  line-height: 26px;
  margin-bottom: 5px;
  display: block; }

.slider-gallery {
  .slick-list {
    margin: 0 -15px; }
  .slider-slide {
    padding: 20px 15px; } }

.arrow-hidden {
  .slick-next, .slick-prev {
    display: none !important; } }

.slider-gallery-wrapp {
  overflow: hidden;
  .slider-gallery {
    overflow: visible;
    .slick-track, .slick-list {
      overflow: visible; } } }

.tabs__box_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0,0,0,0); }

.soc-img {
  display: block;
  text-decoration: none;
  width: 100%;
  max-width: 200px;
  border-radius: 10px;
  overflow: hidden;
  outline: none;
  transition: all 0.1s linear;
  position: relative;
  top: 0;
  left: 0;
  &:hover {
    box-shadow: 2px 2px 5px rgba(#000, 0.6);
    img {
      transform: scale(1.04); } }
  img {
    width: 100%;
    transition: all 0.1s linear; } }



.soc-slider-wrapp {
  overflow: hidden;
  padding: 10px 10px 0; }
.soc-slider {
  .slider-slide {
    padding: 10px;
    > * + * {
      margin-top: 10px; } }

  .slick-list {
    margin-left: -10px;
    margin-right: -10px; } }

.slider_tabs {
  .t-card__img:before {
    background-image: linear-gradient(to bottom, rgba(#f8fbfa, 0) 0%, rgba(#f8fbfa, 1) 100%); } }

.soc-img {
  height: 300px;
  width: 100%;
  >img {
    height: 100%;
    width: 100%;
    object-fit: cover; } }
/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .banner-social {
    .banner-form__grid {
      background-color: $cl-3; } }

  .slider-slide {
    .card-project {
      width: 290px; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .banner-social {
    padding: 45px 40px;
    p {
      max-width: 580px; } }

  .card-project__open {
    font-size: 16px;
    line-height: 26px; }

  .card-project__name {
    font-size: 24px;
    line-height: 30px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner-social {
    padding: 51px 40px 45px;
    max-width: 1300px;
    margin: -200px auto 0;
    position: relative;
    z-index: 1;
    p {
      font-size: 18px;
      font-weight: 500;
      line-height: 30px;
      color: rgba(255, 255, 255, 0.9);
      margin-bottom: 45px; }
    .title-site_h2, h1, h2 {
      line-height: 1.2; } }

  .card-project__open {
    font-size: 16px;
    line-height: 20px; }

  .card-project__name {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 10px; }

  .slider-gallery .card-project {
    width: 300px; }
  .card-project {
    height: 330px; }

  .card-project {
    position: relative;
    top: 0;
    box-shadow: 0 0 0 rgba(#000,0);
    transition: all 0.2s linear;

    &:hover, &:focus {
      top: -20px;
      box-shadow: 0 12px 19px rgba(50, 78, 67, 0.1), 0 6px 11px rgba(50, 78, 67, 0.2);
      .card-project__open {
        opacity: 1; } } }
  .card-project__open {
    opacity: 0;
    transition: all 0.2s linear; }

  .soc-slider-wrapp {
    padding: 15px 0 0; }
  .soc-slider {
    .slider-slide {
      padding: 15px;
      > * + * {
        margin-top: 30px; } }
    .slick-list {
      margin-left: 0;
      margin-right: 0; } }
  .soc-img {
    width: 290px;
    max-width: none;
    height: 400px; }

  /* end media min width 1000 pixels */ }

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .cap-section_desc-center, .slider-cap-nav .cap-section_desc-center {
    text-align: center;

    .title-site_alone {
      margin-bottom: 0; } }
  .slider-cap-nav .cap-section_desc-center + {
    .team-slider-nav {
      margin-bottom: 70px; } }

  /* end media min width 1200 pixels */ }

/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .slider-cap-nav .cap-section_desc-center + {
    .team-slider-nav {
      margin-bottom: 104px; } }
  /* end media min width 1350 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .soc-slider-wrapp {
    padding: 40px 0 0; }
  .soc-slider {
    .slider-slide {
      padding: 15px 20px;
      > * + * {
        margin-top: 40px; } }
    .slick-list {
      margin-left: 0;
      margin-right: 0; } }
  .soc-img {
    width: 360px;
    max-width: none; }
  /* end media min width 1440 pixels */ }


/* Min width 1900 pixels */
@media only screen and (min-width: 1900px) {
  .social-image {
    img {
      width: 100%; } }
  .soc-img {
    height: 500px; }
  /* end media min width 1900 pixels */ }

/* ----- END index page /--------------- */
