/* ----- Banner comp ------------------- */

.banner-box {
  background: url(../img/bg-img1.jpg) left 80px center no-repeat;
  background-size: auto 100%; }

.txt-shadow {
  color: $cl-29;
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  margin-bottom: 20px; }

.banner-box__content {
  min-height: 440px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 20px 0;
  .title-site, h1, h2, h3, h4, h5, h6 {
    color: $cl-8;
    text-shadow: 0 0 3px rgba(#fff, 0.4);
    font-weight: normal; } }

.form-group_desc {
  color: $cl-8;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px; }

.col-rose {
  color: $cl-13; }

.banner-box-img {
  display: none; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .banner-form__grid {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: $cl-25;
    border-radius: 5px;
    margin-bottom: 15px;
    max-width: 600px;
    .form-group {
      margin-bottom: 0;
      flex-grow: 1; }
    .form-group_btn-form {
      flex: 0 0 auto; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .txt-shadow {
    font-size: (24*0.8)+px;
    line-height: (29*0.8)+px; }

  .form-group_desc {
    font-size: 14px;
    line-height: 20px; }

  .banner-box {
    background-position: right -80px center; }
  .banner-box__content {
    min-height: 500px;
    padding: 50px 0;
    .title-site, h1, h2, h3, h4, h5, h6 {
      margin-bottom: 40px; } }

  .banner-form__grid {
    .form-group__field {
      color: $cl-20;
      font-size: 16px;
      padding: 0 20px; }
    .form-group_btn-form {
      .btn {
        min-width: 290px;
        font-weight: bold; } } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner-box__content {
    min-height: 835px;
    .title-site, h1, h2, h3, h4, h5, h6 {
      margin-bottom: 60px; } }

  .banner-box {
    background-position: right -80px center; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .banner-box {
    background: none;
    position: relative;
    overflow: hidden;
    z-index: 1;
    &__content {
      position: relative;
      z-index: 2; } }

  .banner-box-img {
    display: block;
    position: absolute;
    overflow: hidden;
    bottom: 0;
    top: 0;
    left: 50%;
    width: (1170*0.8)+px;
    margin-left: (-395*0.8)+px;
    background: url(../img/b-house.jpg) center center no-repeat;
    background-size: cover;
    z-index: -1; }

  .banner-box-img__wood {
    position: absolute;
    top: -139px;
    right: -146px;
    bottom: 0;
    left: auto; }

  .banner-box-img__flowers {
    position: absolute;
    bottom: -574px;
    right: 0;
    left: 0;
    text-align: center;
    margin-left: -50px; }

  .banner-box-img__primery {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    img {
      min-width: 100%;
      height: 100%; } }

  .banner-box__content {
    min-height: (900*0.8)+px;
    padding-left: 0; }
  /* end media min width 1200 pixels */ }

/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .banner-box {
    background: url(../img/b-left-bg.jpg) left center no-repeat; }
  /* end media min width 1350 pixels */ }

/* Min width 1900 pixels */
@media only screen and (min-width: 1900px) {
  .banner-box {
    background: url(../img/b-left-bg.jpg) left center no-repeat, url(../img/b-right-bg.jpg) right center no-repeat; }
  /* end media min width 1900 pixels */ }


/* media height!!!!!!!!! Min width 1200 pixels */
@media only screen and (min-width: 1200px) and (max-height: 1100px) {
  .banner-box__content {
    height: calc(100vh - 91px);
    min-height: 600px; }
  /* end media min width 1200 pixels */ }

/* media height!!!!!!!!! Min width 1200 pixels */
@media only screen and (min-width: 1200px) and (max-height: 850px) {
  .banner-box-img {
    width: 920px;
    margin-left: -280px; }
  /* end media min width 1200 pixels */ }

/* media height!!!!!!!!! Min width 1200 pixels */
@media only screen and (min-width: 1200px) and (max-height: 790px) {
  .banner-box-img {
    width: 810px;
    margin-left: -286px; }
  /* end media min width 1200 pixels */ }

/* media height!!!!!!!!! Min width 1200 pixels */
@media only screen and (min-width: 1200px) and (min-height: 850px) and (max-height: 1100px) {
  .banner-box-img {
    width: 936px;
    margin-left: -316px;
    height: 720px;
    margin-top: auto;
    margin-bottom: auto; }
  /* end media min width 1200 pixels */ }



