/*_________________F A N C Y_______________________*/

@import "standard/fancy";

.fancybox-close-small {
  background: transparent;
  border: 0;
  border-radius: 0;
  color: #000;
  cursor: pointer;
  height: 44px;
  margin: 0;
  padding: 6px;
  position: absolute;
  right: 0;
  top: 0;
  width: 44px;
  z-index: 10;

  svg {
    fill: transparent;
    opacity: .8;
    stroke: currentColor;
    stroke-width: 1.5;
    transition: stroke .1s; }

  &:focus {
    outline: none; }

  &:hover svg {
    opacity: 1; } }

.fancybox-slide--image .fancybox-close-small, .fancybox-slide--video .fancybox-close-small, .fancybox-slide--iframe .fancybox-close-small {
  color: #ccc;
  padding: 5px;
  right: -12px;
  top: -44px; }

.fancybox-slide--image .fancybox-close-small:hover svg, .fancybox-slide--video .fancybox-close-small:hover svg, .fancybox-slide--iframe .fancybox-close-small:hover svg {
  background: transparent;
  color: #fff; }

.fancybox-is-scaling .fancybox-close-small, .fancybox-is-zoomable.fancybox-can-drag .fancybox-close-small {
  display: none; }

.st-modal {
  max-width: 1000px;
  width: 95%;
  margin: 0 auto;
  display: none;
  padding: 50px 10px 30px;
  position: relative;
  &_sent {
    box-shadow: 0 10px 14px rgba(11, 38, 7, 0.02), 0 43px 68px rgba(8, 29, 5, 0.06), 0 4px 4px rgba(0, 0, 0, 0.25);
    padding: 0;
    overflow: hidden;
    border-radius: 10px;
    max-width: 400px; } }

.sent-img {
  display: none; }

.sent-txt {
  background-color: $cl-3;
  text-align: center;
  padding: 40px 15px;
  >* + * {
    margin-top: 10px; } }

.txt1 {
  font-size: 26px;
  line-height: 1.2;
  font-family: $site-font-family-3;
  font-weight: normal;
  margin-bottom: 10px; }

.txt2 {
  font-size: 18px;
  line-height: 1.2;
  font-family: $site-font-family;
  font-weight: normal; }

.txt3 {
  font-size: 16px;
  line-height: 1.2;
  font-family: $site-font-family;
  font-weight: normal; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .st-modal {
    padding: 50px 40px 30px;
    &_sent {
      padding: 0; } }

  .sent-txt {
    padding: 40px 30px;
    >*+* {
      margin-top: 15px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .st-modal_sent {
    .fancybox-close-small {
      padding: 0;
      color: #000;
      width: 50px;
      height: 50px;
      svg {
        stroke-width: 2; } } }
  .sent-grid {
    display: flex;
    justify-content: space-between;
    &__left {
      flex: 0 0 49%; }
    &__right {
      flex: 0 0 49%; }
    .sent-txt {
      display: flex;
      justify-content: center;
      flex-direction: column;
      text-align: left;
      position: relative;
      z-index: 3; } }

  .sent-img {
    display: block;
    position: relative;
    z-index: 1;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    &:after {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 0;
      top: 0;
      width: 300px;
      background-image: linear-gradient(to right, rgba(#fff,1) 0%, rgba(#fff, 0) 100%); } }

  .txt1 {
    font-size: 32px; }
  .txt2 {
    font-size: 24px; }
  .txt3 {
    font-size: 18px; }

  .st-modal_sent {
    max-width: 1170px; }

  .sent-txt {
    padding: 40px;
    > * + * {
      margin-top: 30px; }
    .txt2 {
      margin-bottom: 13px; } }

  /* end media min width 1000 pixels */ }


// mainstyles
.fancybox-inner {
  .fancybox-toolbar {
    .fancybox-button.fancybox-button--zoom, .fancybox-button.fancybox-button--thumbs {
      display: none !important; } } }

/*_____________end F A N C Y_______________________*/
