/* ----- maps ------------------- */

.main-map {
  min-height: 318px;
  border-radius: 15px; }

.main-map_big {
  min-height: 400px;
  margin: 40px -15px; }

/* Google MAP hidden nav */
.gm-style-cc {
  display: none; }

//jandex
.ymaps-2-1-71-map-copyrights-promo, .ymaps-2-1-71-copyright {
  display: none !important; }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-map_big {
    min-height: 400px;
    margin: 60px -15px 0; }
  /* end media min width 1000 pixels */ }


