/* ----- Review comp ------------------- */

.review-img {
  width: 196px;
  height: 196px;
  margin: 0 auto;
  border-radius: 50%;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.review-box__cap {
  padding: 40px;
  position: relative;
  background: url(../img/bg-review.png) left calc(50% + 6px) top calc(50% - 9px) no-repeat; }


.review-box__info {
  text-align: center;
  .icon-cards {
    margin-top: 30px; } }

.review-box__name {
  color: $cl-6;
  font-size: 24px;
  line-height: 30px;
  margin-bottom: 10px; }

.review-box__txt {
  color: $cl-4;
  font-size: 14px;
  line-height: 18px; }

.icon-card__txt {
  color: $cl-4;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  b {
    color: $cl-6;
    font-weight: 500; } }

.icon-card {
  border-radius: 5px;
  border: 1px solid $cl-11;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 13px;
  margin-bottom: 10px;
  &__icon {
    min-width: 50px;
    margin-right: 10px;
    text-align: center; }
  &__txt {
    text-align: left; } }


.review-slider {
  .slick-prev, .slick-next {
    top: 100px;
    margin-top: 0; } }

.review-slider-wrapp {
  padding-bottom: 80px;
  .slick-dots {
    bottom: -50px; } }

.p-card {
  text-align: center;
  padding: 0 15px;
  max-width: 500px;
  margin: 0 auto;
  &__info {
    margin-top: -20px; }
  &__img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    margin: 0 auto;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } } }

.p-card__img-wrapp {
  position: relative;
  padding: 40px;
  display: block;
  background: url(../img/p-line.svg) center top 5px no-repeat;
  background-size: contain;
  text-align: center; }

.p-card__name {
  color: $cl-6;
  font-size: 24px;
  line-height: 30px;
  margin-bottom: 5px; }

.p-card__txt {
  color: $cl-4;
  font-size: 14px;
  line-height: 18px; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .icon-cards {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%; }
  .icon-card {
    min-width: 48%;
    max-width: 48%;
    margin-left: 1%;
    margin-right: 1%;
    &_long {
      min-width: 98%;
      max-width: 98%; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .review-box__name {
    font-size: 30px;
    line-height: 40px; }

  .review-box__txt {
    font-size: 16px;
    line-height: 26px; }

  .icon-card__txt {
    font-size: 12px;
    line-height: 20px; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .icon-card {
    min-width: 23%;
    max-width: 23%;
    &_long {
      min-width: 48%;
      max-width: 48%; } }

  .review-slider-wrapp {
    margin-top: 60px; }

  .review-slider {
    .slick-prev {
      margin-left: -486px; }
    .slick-next {
      margin-left: 422px; }
    .slick-prev, .slick-next {
      left: 50%;
      top: 109px; } }

  .review-box__cap {
    padding: 44px; }

  .review-box__txt {
    max-width: 880px;
    margin-left: auto;
    margin-right: auto; }

  .review-box__info .icon-cards {
    margin-top: 43px; }

  .review-slider-wrapp {
    padding-bottom: 130px;
    .slick-dots {
      bottom: -80px; } }

  .p-card__name {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 9px; }

  .p-card__txt {
    font-size: 16px;
    line-height: 22px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .p-card {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    max-width: none; }

  .p-card__img {
    width: 196px;
    height: 196px; }

  .p-card__img-wrapp {
    padding: 20px 30px;
    background-size: 300px auto;
    background-position: -34px -19px; }

  .p-card__info {
    margin-top: 0;
    text-align: left; }
  /* end media min width 1200 pixels */ }

/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .p-card__img-wrapp {
    margin-right: 30px; }
  /* end media min width 1350 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .p-card__name {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 9px; }

  .p-card__txt {
    font-size: 16px;
    line-height: 24px; }

  .slider-review-sm-wrapp {
    max-width: 1700px;
    margin: 0 auto; }
  /* end media min width 1440 pixels */ }

/* ----- END Review comp --------------- */
