/* ----- Services ------------------- */

.s-menu {
  box-shadow: 0 5px 7px rgba(5, 11, 4, 0.01), 0 10px 14px rgba(11, 38, 7, 0.02), 0 43px 68px rgba(8, 29, 5, 0.06);
  border-radius: 10px;
  background-color: $cl-3;
  padding: 20px 0;
  margin-bottom: 40px;
  ul {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    li {
      min-width: 50%;
      max-width: 50%;
      padding: 10px; } }
  ul li {
    color: $cl-4;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    &.active {
      font-weight: 600;
      color: $cl-6;
      &:before {
        right: 10px; } } }
  ul li {
    color: $cl-4;
    text-decoration: none;
    outline: none;
    display: inline-block;
    position: relative;
    z-index: 1;
    &:before {
      content: ' ';
      position: absolute;
      left: 10px;
      right: 100%;
      overflow: hidden;
      height: 4px;
      transition: all 0.2s linear;
      top: 0px;
      border-radius: 5px;
      background-color: $cl-10; }
    &:hover {
      color: $cl-6;
      &:before {
        right: 10px; } } } }

.s-slider-wrapp__slider {
  margin-left: -15px;
  margin-right: -15px; }

.s-slider-img {
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 10px;
  overflow: hidden;
  z-index: 1;
  &:after {
    content: ' ';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50%;
    background: linear-gradient(to bottom, rgba(#fff, 0) 0%, rgba(#fff, 1) 100%); }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.s-slider-wrapp__thumb {
  margin-top: -50px; }

.s-slider-thumb-img {
  width: 100%;
  max-width: 125px;
  margin: 0 auto;
  height: 50px;
  border-radius: 10px;
  overflow: hidden;
  transition: all 0.2s linear;
  top: 0;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.s-slider-thumb {
  .slick-track {
    margin-left: 0; }
  .slick-list {
    margin-left: -7px;
    margin-right: -7px;
    .slider-slide {
      padding: 11px 7px; } }
  .s-slider-thumb-img {
    &:hover {
      position: relative;
      top: -8px; } }
  .slick-current {
    .s-slider-thumb-img {
      position: relative;
      top: -11px;
      box-shadow: 0 12px 19px rgba(50, 78, 67, 0.1), 0 6px 11px rgba(50, 78, 67, 0.2);
      box-shadow: 5px 5px 10px rgba(#000, 0.5); } } }

.slider-desc {
  max-width: 970px;
  margin: 20px 0;
  &__txt {
    margin-bottom: 20px;
    color: $cl-6;
    font-size: 12px;
    line-height: 14px; }
  &__btn {
    margin-bottom: 20px;
    color: $cl-6;
    font-size: 16px;
    line-height: 24px; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .s-menu {
    ul {
      li {
        min-width: 33.3%;
        max-width: 33.3%; } } }

  .s-slider-thumb-img {
    height: 86px; }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .s-menu {
    ul {
      li {
        min-width: 25%;
        max-width: 25%; } } }

  .s-slider-img {
    height: 500px; }

  .slider-desc {
    max-width: 970px;
    margin: 20px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__txt {
      margin-bottom: 0;
      font-size: 16px;
      line-height: 24px;
      margin-right: 100px; }
    &__btn {
      margin-bottom: 20px;
      color: $cl-6;
      font-size: 16px;
      line-height: 24px; } }
  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .s-menu {
    ul {
      li {
        min-width: 20%;
        max-width: 20%;
        font-size: 14px;
        line-height: 22px; } } }

  .section-st {
    &_services {
      padding: 130px 0; } }

  .s-slider-img {
    height: (600*0.8)+px; }

  .slider-desc {
    margin: 50px auto; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .s-menu {
    padding: 10px 20px 10px 20px;
    margin-bottom: 60px;
    ul {
      li {
        padding: 20px 10px;
        &:before {
          top: 0px; } } } }

  .s-slider-wrapp__slider {
    margin: 0; }
  .s-slider-wrapp__thumb {
    padding: 0 30px; }
  /* end media min width 1200 pixels */ }


/* ----- END Services --------------- */
