/* ----- Page structure ---------------- */
.clear {
  clear: both; }

.group:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden; }

#wrapper {
  min-width: 320px; }

.section-st {
  padding: 50px 0; }

.container {
  padding-left: 15px;
  padding-right: 15px;
  width: auto; }

.visible-lap, .hidden-full, .hidden-mobile, .mobile-hidden {
  display: none; }

.about-bg {
  position: relative;
  z-index: 2; }

.inner-section {
  background: url(../img/bg1.jpg) center top no-repeat;
  background-size: 100% auto; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .hidden-mobile {
    display: block; }

  .hidden-tabl {
    display: block; }

  .mobile-hidden {
    display: block; }

  .section-st_step-works, .section-st_team {
    position: relative;
    z-index: 2; }

  .section-st_review {
    position: relative;
    z-index: 3; }

  .bg-section {
    position: relative;
    z-index: 1;
    &__services {
      position: absolute;
      top: -430px;
      z-index: -1;
      bottom: 90px;
      left: 0;
      right: 0;
      background: url(../img/bg-review3.jpg) center top no-repeat;
      background-size: cover; }
    &__skills-and-certificat {
      position: absolute;
      top: 190px;
      z-index: -1;
      bottom: -80px;
      left: 0;
      right: 0;
      background: url(../img/bg-skills.jpg) center bottom no-repeat;
      background-size: cover; }
    &__team {
      position: absolute;
      top: 100px;
      z-index: -1;
      bottom: -240px;
      left: 0;
      right: 0;
      background: url(../img/bg-review2.jpg) center bottom no-repeat;
      background-size: cover;
      &.bg-section__team_second {
        top: auto;
        bottom: auto;
        height: 1400px;
        top: 50%;
        margin-top: -700px; } } }


  .section-st_team + .section-st {
    position: relative;
    z-index: 3; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  html, body {
    height: 100%; }

  body {
    display: flex;
    flex-direction: column; }

  #wrapper {
    flex: 1 0 auto; }

  .footer {
    flex-shrink: 0; }

  .section-st {
    padding: 60px 0; }

  .visible-lap {
    display: block; }

  .hidden-lap {
    display: none; }

  .bg-section {
    &__team {
      bottom: -320px; } }

  .container {
    max-width: 966px;
    margin: 0 auto; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .section-st_lg {
    padding: 80px 0; }
  .hidden-lap {
    display: block; }

  .bg-section {
    &__skills-and-certificat {
      top: 30px;
      bottom: -90px; } }

  .section-st.section-st_first-inner {
    padding-top: 100px; }

  #wrapper.sticky {
    padding-top: 85px;
    .header {
      position: fixed;
      top: -100%;
      left: 0;
      right: 0;
      z-index: 200;
      background-color: #fff;
      box-shadow: 0 0 4px rgba(#000, 0.5);
      animation: {
        name: header-anim;
        duration: 1s;
        timing-function: ease-in-out;
        fill-mode: forwards; } } }

  @keyframes header-anim {
    to {
      top: 0; } }
  /* end media min width 1200 pixels */ }

/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .text-bl__img_long {
    margin-left: -50px;
    margin-right: -50px; }

  .section-st.section-st_first-inner {
    padding-top: 125px; }
  /* end media min width 1350 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .bg-section {
    &__skills-and-certificat {
      top: -200px;
      bottom: -30px;
      background-position: center bottom; } }

  .section-st_skills {
    padding-bottom: 80px; }

  div.c-cards {
    margin: 110px 0 160px; }

  .text-bl__img_long {
    margin-left: -63px;
    margin-right: -63px; }
  /* end media min width 1440 pixels */ }


/* Min width 1900 pixels */
@media only screen and (min-width: 1900px) {
  .bg-section__skills-and-certificat {
    top: -200px;
    bottom: -100px; }
  /* end media min width 1900 pixels */ }

/* Min width 2000 pixels */
@media only screen and (min-width: 2000px) {
  .bg-section__skills-and-certificat {
    top: -580px;
    bottom: -50px; }
  /* end media min width 2000 pixels */ }

/* Min width 2500 pixels */
@media only screen and (min-width: 2500px) {
  .bg-section {
    &__team {
      bottom: -510px; } }
  /* end media min width 2500 pixels */ }

/* Min width 2500 pixels */
@media only screen and (min-width: 2500px) {
  #wrapper {
    width: 2400px;
    margin: 0 auto; }
  /* end media min width 2500 pixels */ }
























