/* ----- Inner next ------------------- */
.card-p-list {
  margin-bottom: 30px; }

.card-project-box-wrapp {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px; }

.card-project-box__hidden {
  display: none; }

.p-main-box {
  margin: 40px 0; }

.p-main {
  margin-bottom: 20px;
  display: block;
  text-decoration: none;
  outline: none; }

.p-main-info {
  padding: 5px 0;
  > * + * {
    margin-top: 5px; }
  &__tt {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    color: #272727; }
  &__txt {
    font-size: 14px;
    line-height: 18px;
    color: #868686;
    font-weight: 500; } }
.p-main__img {
  height: 250px;
  overflow: hidden;
  border-radius: 10px;
  display: block;
  text-decoration: none;
  outline: none;
  img {
    transition: all 0.2s linear;
    width: 100%;
    height: 100%;
    object-fit: cover; }

  &:hover {
    img {
      transform: scale(1.1); } } }



/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .card-project-box-wrapp {
    padding: 0 5px; }
  .card-p-list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    .card-project {
      min-width: 48%;
      max-width: 48%;
      margin-left: 1%;
      margin-right: 1%; }
    .card-project_long {
      min-width: 98%;
      max-width: 98%; } }
  .p-main-info {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    &__tt {
      margin-right: 20px; } }

  .p-main-list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;
    &__item {
      flex: 0 0 50%;
      max-width: 50%;
      padding: 0 5px; }
    .p-main-info {
      display: block;
      &__tt {
        margin-right: 0; } } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .card-p-list {
    .card-project {
      margin-bottom: 15px; } }

  .p-main-info {
    padding: 13px 0; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .card-p-list {
    .card-project {
      min-width: 31.3%;
      max-width: 31.3%;
      margin-bottom: 25px; }
    .card-project_long {
      min-width: 98%;
      max-width: 98%; }
    .card-project_two-thirds {
      min-width: 56%;
      max-width: 56%; }
    .card-project_one-thirds {
      min-width: 40%;
      max-width: 40%; } }

  .card-project_second {
    &:hover, &:focus {
      top: 0; }
    .card-project__open {
      opacity: 1; } }

  .about-txt-box_second {
    > * + * {
      margin-top: 5px; } }

  .p-main__img {
    height: 350px; }

  .p-main-info {
    padding: 20px 30px;
    > * + * {
      margin-top: 10px; }
    &__tt {
      font-size: 28px;
      line-height: 34px; }
    &__txt {
      font-size: 16px;
      line-height: 20px; } }

  .p-main-box {
    margin-top: 60px; }

  .p-main-list {
    margin-left: 0;
    margin-right: 0;
    padding: 0 20px;
    margin-bottom: 40px;
    &__item {
      flex: 0 0 33.3%;
      max-width: 33.3%;
      padding: 0 10px; }
    .p-main-info__tt {
      font-size: 22px;
      line-height: 30px; }
    .p-main-info__txt {
      font-size: 14px;
      line-height: 18px; }
    .p-main-info {
      padding: 20px 20px; }
    .p-main__img {
      height: 250px; } }

  .card-project_second {
    img {
      display: block;
      transition: all 0.1s linear; }
    .card-project-author {
      margin-bottom: -66px;
      margin-top: 20px;
      transition: all 0.1s linear; }

    &:hover {
      > img {
        transform: scale(1.05); }
      .card-project-author {
        margin-bottom: -10px;
        margin-top: 10px; } } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .card-project-box-wrapp {
    padding-left: 100px;
    margin-top: 60px;
    .card-project_left {
      margin-left: -100px; }
    .card-project-box__btn {
      margin-right: 100px; } }

  .about-txt-box_sm-width {
    padding-left: 100px;
    padding-right: 100px; }
  /* end media min width 1200 pixels */ }


