// Typography var
$site-font-style: normal;   //normal | italic | oblique | inherit
$site-font-variant: normal; //normal | small-caps | inherit
$site-font-weight: normal;
$site-fill-color: #fff;

$site-font-family: 'Montserrat', Arial, sans-serif;
$site-font-family-2: 'Open Sans', Arial, sans-serif;
$site-font-family-3: 'Noto Serif', Arial, sans-serif;
$site-font-family-4: 'Roboto', Arial, sans-serif;

$body-text-size: 14+(px)+/20+(px);
$site-text-size: 14+(px);
$site-line-height: 20+(px);

// $font-base: 'Roboto'
// $font-second: 'Roboto'

$site-text-color: #000;

// Title site variables
$site-title-color: $cl-8;
$title-font-weight: $site-font-family-3;
$title-font-family: 500;
$title-line-height: 1.2+(em);

$site-h1-size-mobile: 30+(px);
$site-h2-size-mobile: 26+(px);
$site-h3-size-mobile: 24+(px);
$site-h4-size-mobile: 20+(px);
$site-h5-size-mobile: 18+(px);
$site-h6-size-mobile: 16+(px);

$site-h1-size: (62*0.8)+(px);
$site-h2-size: (62*0.8)+(px);
$site-h3-size: (36*0.8)+(px);
$site-h4-size: 24+(px);
$site-h5-size: 16+(px);
$site-h6-size: 14+(px);


// Buttons variables
$btn-fill-color: $cl-18;
$btn-fill-hover-color: lighten($btn-fill-color, 5%);
$btn-fill-second-color: $cl-13;
$btn-fill-second-hover-color: darken($btn-fill-second-color, 5%);
$btn-color: $cl-24;
$btn-color-hover: $cl-24;

$btn-radius: 30+(px);
$btn-font: normal 600 14+px+/+24+px $site-font-family;
$btn-padding: ((38-24)/2)+px 20px;
$btn-width-min: 210px;

//border btn
$btn-border: 1px solid $cl-6;
$btn-border-hover-color: $btn-fill-color;
$btn-border-line-height: 22px;
$btn-border-color-text: $btn-fill-color;
$btn-border-hover-color-text: $btn-color;

//sm
$btn-padding-sm: ((38-24)/2)+px 15px;
$btn-width-min-sm: 110px;
//md
$btn-padding-md: ((50-24)/2)+px 25px;
$btn-width-min-md: 150px;
//lg
$btn-padding-lg: ((60-24)/2)+px 30px;
$btn-width-min-lg: 200px;
//font-radius-sm
$btn-size-sm: 12px;
//font-radius-md
$btn-size-md: 16px;
//font-radius-lg
$btn-size-lg: 18px;
//font-family-bold
$btn-font-family-bold: $site-font-family;
$btn-font-weight-bold: 700;
//font-family-light
$btn-font-family-light: $site-font-family;
$btn-font-weight-light: 300;
//font-radius-sm
$btn-radius-sm: 5px;

// links variables
$color-link: #4a93df;
$color-h-link: darken($color-link, 5%);

// color .placeholder
$st-color-placeholder: $cl-20;

// Field Forms variables Input and Textarea
$input-height: 40+(px);
$input-padding: 0 15+(px);
$input-border: 1px solid $cl-25;
$input-font: 14px $site-font-family;
$input-color: #222;
$input-fill-color: $cl-25;
$input-radius: 5px;
$input-shadow: none;

$input-hover-border-color: darken($cl-25, 5%);
$input-focus-border-color: darken($cl-25, 10%);

// Navigation
$burger-fill: $cl-dark;
$burger-fill-open: $cl-light;

$font-menu-item-mobile: normal normal 20+(px)+/24+(px) $site-font-family;
$font-menu-item: normal normal 16+(px)+/20+(px) $site-font-family;
$fill-menu: $cl-site;
$fill-menu-hover: darken($cl-site, 5%);

// Field Forms textarea
$textarea-height: 130+(px);
$textarea-padding: 10+(px) 15+(px);

// breakpoint
$mob: 375px;
$mob-md: 480px;
$mob-lg: 640px;
$tabl: 768px;
$lap: 1000px;
$desk: 1200px;
$desk-md: 1600px;
$desk-lg: 1900px;

