/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: $btn-radius;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: $btn-color;
  background-color: $btn-fill-color;
  path {
    fill: $btn-color; }

  &:hover {
    text-decoration: none;
    color: $btn-color-hover;
    background-color: #fdcd97;
    box-shadow: 0px 4px 11px #FCE8D1;
    path {
      fill: $btn-color-hover; } }

  &_second-st {
    background-color: $btn-fill-second-color;
    color: #fff;
    path {
      fill: #fff; }

    &:hover {
      background-color: #e378a2;
      box-shadow: 0 4px 15px #e99ebc;
      color: #fff;
      path {
        fill: #fff; } } }

  &_three-st {
    background-color: $cl-17;
    color: rgba(54, 120, 81, 0.9);
    font-weight: 400;
    font-family: $site-font-family-2;
    path {
      fill: rgba(54, 120, 81, 0.9); }

    &:hover {
      background-color: $cl-7;
      color: $cl-3;
      box-shadow: none;
      path {
        fill: $cl-3; } } }

  &_four-st {
    background-color: #c93973;
    color: rgba(255, 255, 255, 0.9);
    font-weight: 400;
    font-family: $site-font-family-2;
    path {
      fill: rgba(255, 255, 255, 0.9); }

    &:hover {
      background-color: #af255d;
      box-shadow: none;
      color: rgba(255, 255, 255, 0.9);
      path {
        fill: rgba(255, 255, 255, 0.9); } } }

  &_five-st {
    background-color: #3b5999;
    color: rgba(255, 255, 255, 0.9);
    font-weight: 400;
    font-family: $site-font-family-2;
    path {
      fill: rgba(255, 255, 255, 0.9); }

    &:hover {
      background-color: #2551af;
      box-shadow: none;
      color: rgba(255, 255, 255, 0.9);
      path {
        fill: rgba(255, 255, 255, 0.9); } } }

  &_border {
    border: $btn-border;
    color: $cl-6;
    line-height: $btn-border-line-height;
    background: transparent;

    &:hover {
      color: $cl-3;
      background-color: $cl-6;
      box-shadow: none; }

    &.btn_second-st {
      border-color: $btn-fill-second-color;
      color: $btn-fill-second-color;

      &:hover {
        color: $cl-3;
        background-color: $btn-fill-second-color; } } }

  &_norad {
    border-radius: 5px; }

  &_min-width {
    min-width: $btn-width-min; }
  &_min-width_2 {
    min-width: 200px; }
  &_min-width_3 {
    min-width: 260px; }

  &_max {
    width: 100%; }

  &_sm {
    padding: $btn-padding-sm;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_md {
    padding: $btn-padding-md;

    &.btn_min-width {
      min-width: $btn-width-min-md; } }

  &_lg {
    padding: $btn-padding-lg;

    &.btn_min-width {
      min-width: $btn-width-min-lg; } }

  &__icon-text {
    display: inline-block;
    vertical-align: middle; }

  &__icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;
    line-height: 1px;

    &_right {
      margin-left: 6px; }

    &_left {
      margin-right: 6px; } }

  &_font-bold {
    font-weight: 700; }

  &_font-semi {
    font-weight: 600; }

  &_font-medium {
    font-weight: 500; }

  &_font-normal {
    font-weight: 400; }

  &_size-sm {
    font-size: 14px; }
  &_size-md {
    font-size: 16px; }
  &_size-lg {
    font-size: 18px; }

  &_up {
    text-transform: uppercase; }

  &_radius-sm {
    border-radius: 5px; } }

input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: $input-height;
  width: auto;
  &_max {
    width: 100%; } }

.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: $input-height;
  width: 100%;
  padding: $input-padding;
  border: $input-border;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: $textarea-height;
  padding: $textarea-padding;
  resize: none; }

/* ----- END controls ------------------- */
