/* ----- Navigation -------------------- */
/* -- Bread crumbs ------------------ */
.bread-crumbs {
  padding: 0;
  list-style-type: none;
  display: none;

  @include tabl {
    display: block; }

  li {
    display: inline-block;
    vertical-align: middle;
    margin: 0;

    &:before {
      content: '/';
      margin: 0 5px; }

    &:first-child {
      &:before {
        display: none; } } } }


/* -- Pagination -------------------- */
.pagination {
  list-style-type: none;

  li {
    display: inline-block;
    vertical-align: middle;
    margin: 10px 5px; }

  a {
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 2px;
    background-color: $cl-gallery;
    line-height: 30px;
    text-align: center;
    text-decoration: none;
    &:hover {
      background-color: #fc8a00;
      color: #fff; } }

  &__prev,
  &__next {
    a {
      width: auto;
      height: auto;
      border-radius: 0;
      background: none;

      &:hover {
        background: none;
        color: #000; } } }

  &__active {
    a {
      background-color: #fc8a00;
      color: #fff; } } }


/* -- Main menu --------------------- */
.menu-open-wrapper-page {
  overflow: hidden;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0; }

.burger-menu {
  display: block;
  position: absolute;
  right: 5px;
  top: 24px;
  z-index: 101;
  cursor: pointer;
  padding: 10px; }

.burger-menu-box {
  display: block;
  width: 30px;
  padding: 8px 0; }

.burger-menu-inner {
  display: block;
  position: relative;
  height: 3px;
  background-color: $burger-fill;
  border-radius: 3px;
  transition: transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);

  &:before,
  &:after {
    content: ' ';
    position: absolute;
    left: 0;
    height: 3px;
    background-color: $burger-fill;
    width: 100%;
    border-radius: 3px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform; }

  &:before {
    top: -8px;
    transition: top 75ms ease .12s, opacity 75ms ease; }

  &:after {
    bottom: -8px;
    transition: bottom 75ms ease .12s, transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19); } }

.menu-burger--opened {
  .burger-menu {
    position: fixed; }

  .burger-menu-inner {
    transition-delay: 0.12s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: rotate(45deg);

    &:before {
      top: 0;
      transition: top 75ms ease, opacity 75ms ease 0.12s;
      opacity: 0; }

    &:after {
      bottom: 0;
      transition: bottom 75ms ease, transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) .12s;
      transform: rotate(-90deg); } }

  .burger-menu-inner, .burger-menu-inner:after, .burger-menu-inner:before {
    background-color: $burger-fill-open; } }

.menu-wrapper-fixed {
  position: fixed;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  left: -100%;
  transition: left 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000); }

.menu-open-wrapper {
  padding: 20px;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transition: background 0.25s ease-out; }

.menu-burger--opened {
  .menu-wrapper-fixed {
    left: 0; }

  .menu-open-wrapper {
    background-color: darken(#5abf81, 10%); }

  .menu-item {
    margin-left: 0;
    @for $i from 1 through 10 {
      &:nth-child(#{$i}) {
        transition-delay: (0.06s * $i) - 0.06s; } } } }

.header-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 100%; }

.menu {
  margin: 0;
  padding: 0;
  list-style: none; }

.menu-item {
  position: relative;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  list-style-type: none;
  font: $font-menu-item-mobile;
  color: #fff;
  width: 100%;
  margin-left: -100%;
  transition: all 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000);

  &__link {
    color: #fff;
    text-decoration: none;

    &:hover {
      color: #fff; } }
  &.active {
    .menu-item__link {
      cursor: default;
      font-weight: 700; } }

  .icon-point {
    color: #fff; }

  .head-adr__item {
    margin-bottom: 20px; }

  .icon-point {
    justify-content: center;
    font-size: inherit;
    line-height: inherit;
    &:hover, &:focus {
      color: #fff; } } }


/* - Drop down main menu -------- */
.menu-item__link_dd-open {
  padding-left: 20px;
  &:after {
    content: ' ';
    @include corner-bottom(5px, 5px, 5px, #fff);
    border-top-color: #fff;
    margin: 0 0 0 10px; } }

.menu-dd {
  display: none;
  margin: 0;
  padding: 15px 0 5px;
  list-style: none; }

.menu-dd-item {
  line-height: 20px;
  margin: 12px 0;
  position: relative;

  &__link {
    color: #fff;
    text-decoration: none;
    font-size: 16px;

    &:hover {
      color: #fff; } } }

.menu-item_dd-opened {
  .menu-dd {
    display: block; }

  .menu-item__link_dd-open {
    &:after {
      transform: rotate(180deg); } } }

.menu-dd-item__img {
  display: none; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .menu-item {
    .head-adr {
      display: none; } }
  /* end media min width 480 pixels */ }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .menu-item_mobile.menu-item {
    display: none; }
  /* end media min width 480 pixels */ }



/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .menu-burger--opened .menu-open-wrapper {
    background-color: transparent; }
  .menu-open-wrapper-page {
    overflow: visible;
    position: relative;
    height: auto;
    width: auto; }

  .menu-wrapper-fixed {
    position: relative;
    left: 0;
    top: 0;
    height: auto;
    transition: none;
    width: 100%; }

  .menu-open-wrapper {
    background: none;
    padding: 0;
    height: auto;
    overflow: visible; }

  .burger-menu {
    display: none; }

  .header-nav {
    display: block; }

  .menu {
    text-align: left;
    border-radius: 5px; }

  .menu-item {
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    color: $cl-4;
    font-size: 13px;
    line-height: 20px;
    &.active {
      color: $cl-6;
      .menu-item__link {
        color: $cl-6;
        &:after {
          display: none; } } }

    &__link {
      display: block;
      padding: 10px 5px;
      color: $cl-4;
      position: relative;
      &:after {
        content: ' ';
        position: absolute;
        bottom: 0;
        left: 50%;
        right: 50%;
        overflow: hidden;
        height: 3px;
        background-color: transparent;
        transition: left 0.2s linear, right 0.2s linear; }

      &:hover, &:focus {
        color: $cl-6;
        &:after {
          left: 0;
          right: 0;
          overflow: visible;
          background-color: #8ED3A9; } } } }

  .header-inner__right {
    margin-left: 20px; }


  /* - Drop down main menu -------- */
  .menu-item_dd-opened {
    .menu-dd {
      display: none; }

    .menu-item__link_dd-open {
      &:after {
        transform: rotate(0); } } }

  .menu-item__link_dd-open {
    &:after {
      display: none; } }

  .menu-dd {
    background: #FFFFFF;
    box-shadow: 0px 5.38427px 7.10583px rgba(5, 11, 4, 0.00921803), 0px 9.60461px 14.0901px rgba(11, 38, 7, 0.0225732), 0px 43px 68px rgba(8, 29, 5, 0.06);
    border-radius: 10px;
    position: absolute;
    display: none;
    top: 100%;
    left: 50%;
    margin-left: -(1170/2)+px;
    min-width: 1170px;
    max-width: 1170px;
    text-align: left;
    padding: 10px 15px;
    border-radius: 0 0 5px 5px;
    z-index: 120;
    columns: 222px 2;
    column-gap: 40px;
    padding: 40px 610px 30px 60px;

    overflow: hidden;
    min-height: 400px;
    transform-origin: top center;
    animation: {
      name: menu-anim;
      duration: 300ms;
      timing-function: ease-in-out;
      fill-mode: forwards; } }

  .menu-dd-item {
    margin: 0 0 15px;
    font-size: 18px;
    line-height: 30px;
    color: #868686;
    position: static;
    &:first-child {
      .menu-dd-item__img {
        display: block; } }

    &__link {
      display: inline-block;
      color: #868686;
      transition: all 0.2s linear;
      position: relative;
      z-index: 3;
      &:before {
        content: ' ';
        position: absolute;
        left: -15px;
        top: 4px;
        bottom: 4px;
        border-radius: 5px;
        width: 3px;
        transition: all 0.2s linear; }

      &:hover {
        color: #272727;
        font-weight: 600;
        padding-left: 15px;
        &:before {
          left: 0;
          background: #8ED3A9; } } }

    &.active {
      .menu-dd-item__link {
        color: #272727;
        font-weight: 600;
        padding-left: 15px;
        cursor: default;
        &:before {
          left: 0;
          background: #8ED3A9; } }
      .menu-dd-item__img {
        display: block;
        z-index: 1; } }

    &:hover {
      .menu-dd-item__img {
        display: block;
        z-index: 2;
        animation: {
          name: img-anim;
          duration: 0.2s;
          timing-function: linear; } } } }

  .menu-item {
    &:hover {

      .menu-dd {
        display: block; } } }

  .menu-item, .menu-wrapper-fixed {
    position: static; }

  .menu-item_dd {
    &.active {
      .menu-item__link {
        font-weight: bold;
        color: #272727;
        letter-spacing: -0.4px; } }
    .menu-item__link {
      color: #868686; }
    &:hover {
      .menu-item__link {
        color: #272727;
        font-weight: bold;
        letter-spacing: -0.4px; }
      &:after {
        display: block; } }
    &:after {
      display: none;
      content: ' ';
      position: absolute;
      top: 0;
      bottom: 0;
      width: 120px;
      margin-left: -10px; } }

  .menu-dd-item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 600px;
    overflow: hidden;
    &:before {
      content: ' ';
      position: absolute;
      display: block;
      bottom: 0;
      top: 0;
      left: 0;
      width: 360px;
      background: -moz-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
      background: -webkit-linear-gradient(left, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
      background: linear-gradient(to right, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
      z-index: 1; }
    img {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; } }

  @keyframes img-anim {
    from {
      opacity: 0; }
    to {
      opacity: 1; } }

  @keyframes menu-anim {
    0% {
      opacity: 0;
      transform: rotateX(-90deg); }
    100% {
      opacity: 1;
      transform: rotateX(0deg); } }

  /* end media min width 1200 pixels */ }


/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .menu-item {
    font-size: 13px;
    line-height: 18px;
    &__link {
      padding: 10px 10px; } }
  /* end media min width 1350 pixels */ }


/* Min width 1660 pixels */
@media only screen and (min-width: 1660px) {
  .menu-item {
    font-size: 14px;
    line-height: 18px; }
  /* end media min width 1660 pixels */ }

/* ----- END Navigation ---------------- */
