/* ----- Instafeed grid ------------------- */

.bl-scroll {
  overflow: hidden;
  max-height: 400px;
  &_gallery {
    max-height: none;
    overflow: visible; } }

.soc-gallery-wrapp {}

.soc-gallery {
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  a {
    display: block;
    text-decoration: none;
    border-radius: 10px;
    overflow: hidden;
    min-width: 48%;
    max-width: 48%;
    margin: 0 1% 5px;
    height: 200px;
    &:nth-child(n+7) {
      display: none; }

    &:hover, &:focus {
      img {
        transform: scale(1.05); } }
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.2s linear; } } }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .soc-gallery {
    a {
      min-width: 31.3%;
      max-width: 31.3%;
      margin-bottom: 15px; } }
  /* end media min width 640 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .bl-scroll_gallery {
    max-height: 600px;
    margin: 60px 0 20px; }
  .soc-gallery {
    a {
      margin-bottom: 25px; } }
  /* end media min width 1000 pixels */ }

/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .soc-gallery {
    -moz-column-count: 4;
    -moz-column-gap: 3%;
    -moz-column-width: 25%;
    -webkit-column-count: 4;
    -webkit-column-gap: 3%;
    -webkit-column-width: 25%;
    column-count: 4;
    column-gap: 3%;
    column-width: 25%;
    a {
      margin-bottom: 40px; } }
  /* end media min width 1350 pixels */ }


/* Min width 1660 pixels */
@media only screen and (min-width: 1660px) {
  .soc-gallery {
    -moz-column-count: 5;
    -moz-column-gap: 3%;
    -moz-column-width: 16.66%;
    -webkit-column-count: 5;
    -webkit-column-gap: 3%;
    -webkit-column-width: 16.66%;
    column-count: 5;
    column-gap: 3%;
    column-width: 16.66%;
    a {
      margin-bottom: 50px; } }
  /* end media min width 1660 pixels */ }


/* Min width 2000 pixels */
@media only screen and (min-width: 2000px) {
  .soc-gallery {
    -moz-column-count: 6;
    -moz-column-gap: 2%;
    -moz-column-width: 16.66%;
    -webkit-column-count: 6;
    -webkit-column-gap: 2%;
    -webkit-column-width: 16.66%;
    column-count: 6;
    column-gap: 2%;
    column-width: 16.66%;
    a {
      margin-bottom: 40px; } }
  /* end media min width 2000 pixels */ }

/* ----- END Instafeed grid --------------- */
