/* ----- Footer ------------------------ */
.footer {
  padding: 20px 0;
  p {
    color: $cl-4;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px; } }

.f-logo {
  margin-bottom: 20px; }

.social-list__item {
  margin-bottom: 10px; }

.f-grid__half-col {
  margin-bottom: 30px; }

.f-nav {
  margin: 40px 0 20px;
  text-align: center;
  &__menu {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; }
  &__item {
    color: $cl-4;
    font-size: 13px;
    font-weight: 500;
    line-height: 18px;
    &.active {
      color: $cl-6;
      font-weight: 700;
      a {
        color: $cl-6;
        &:hover {
          text-decoration: none; } } }
    a {
      color: $cl-4;
      text-decoration: none;
      outline: none;
      display: inline-block;
      padding: 5px 10px;
      &:hover {
        text-decoration: underline;
        color: $cl-6; } }
    &_sm {
      color: $cl-14;
      font-size: 10px;
      padding-top: 3px;
      a {
        color: $cl-14; } } } }

.f-tt {
  color: $cl-10;
  font-family: $site-font-family;
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 15px; }

.f-list-service {
  columns: 165px auto;
  column-gap: 10px;
  li {
    color: $cl-4;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    margin: 0 0 10px;
    a {
      text-decoration: none;
      outline: none;
      color: $cl-4;
      &:hover, &:focus {
        color: $cl-4;
        text-decoration: underline; } } } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .footer {
    p {
      font-size: 16px;
      line-height: 26px; } }

  .f-grid {
    display: flex;
    justify-content: space-between;
    &__half-col {
      min-width: 49%;
      max-width: 49%; } }

  .social-list {
    display: flex;
    justify-content: flex-start;
    &__item {
      margin-right: 10px; } }

  .f-tt {
    font-size: 36px;
    line-height: 48px; }

  .f-list-service {
    li {
      font-size: 14px;
      line-height: 20px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .footer {
    padding: 40px 0 40px;
    p {
      font-size: 15px;
      line-height: 24px; } }

  .f-logo {
    margin-bottom: 38px; }

  .social-list {
    margin-top: 34px; }

  .social-list__item {
    margin-right: 30px; }

  .f-nav {
    margin-top: 50px;
    margin-left: -15px;
    margin-right: -15px; }

  .f-nav__item_sm {
    margin-left: auto; }

  .f-list-service {
    columns: 180px auto;
    column-gap: 30px; }

  .f-tt {
    padding-top: 10px;
    margin-bottom: 39px; }

  .f-grid__half-col_content {
    padding-left: 60px; }

  .f-list-service li {
    margin-bottom: 12px; }
  /* end media min width 1000 pixels */ }

/* ----- END Footer -------------------- */
