/* Navigation */
$SL-nav-wh: 48px;
$SL-nav-arr-w: 12;
$SL-nav-arr-h: 21;
$SL-nav-arr-left: url(../img/icons/arrow-left.svg);
$SL-nav-arr-right: url(../img/icons/arrow-right.svg);

.slick-prev, .slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  z-index: 1;
  top: 50%;
  width: 36px;
  height: 36px;
  margin: -(36/2)+px 0 0;
  border-radius: 50%;
  padding: 0;
  border: 1px solid $cl-11;
  background-color: #fff;
  outline: none;
  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    height: 16px;
    width: 12px;
    margin-top: -8px; }

  &:hover, &:focus {
    border-color: $cl-7; } }

.slick-prev {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-next {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-prev {
  left: -5px;
  &:hover, &:focus {
    &:before {
      background-image: url("data:image/svg+xml,%3Csvg width='13' height='21' viewBox='0 0 13 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.5332 2.28577L2.42741 8.91199C1.4485 9.71222 1.44849 11.2087 2.42741 12.0089L10.5332 18.6352' stroke='%238ED3AA' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      background-size: 100% 100%; } }
  &:before {
    margin-left: -($SL-nav-arr-w/2+1)+(px);
    background: url("data:image/svg+xml,%3Csvg width='13' height='21' viewBox='0 0 13 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.5332 2.28577L2.42741 8.91199C1.4485 9.71222 1.44849 11.2087 2.42741 12.0089L10.5332 18.6352' stroke='%23A7A7A7' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") center center no-repeat;
    background-size: 100% 100%; } }

.slick-next {
  right: -5px;
  &:hover, &:focus {
    &:before {
      background-image: url("data:image/svg+xml,%3Csvg width='12' height='21' viewBox='0 0 12 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.53955 2.28577L9.64534 8.91199C10.6243 9.71222 10.6243 11.2087 9.64535 12.0089L1.53955 18.6352' stroke='%238ED3AA' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      background-size: 100% 100%; } }
  &:before {
    margin-left: -($SL-nav-arr-w/2-2)+(px);
    background: url("data:image/svg+xml,%3Csvg width='12' height='21' viewBox='0 0 12 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.53955 2.28577L9.64534 8.91199C10.6243 9.71222 10.6243 11.2087 9.64535 12.0089L1.53955 18.6352' stroke='%23A7A7A7' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") center center no-repeat;
    background-size: 100% 100%; } }



.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25; }

.slick-prev.slick-disabled, .slick-next.slick-disabled {
  &:hover, &:focus {
    background-color: #fff;
    border-color: $cl-11; } }

.slick-prev.slick-disabled {
  &:hover, &:focus {
    &:before {
      background-image: url("data:image/svg+xml,%3Csvg width='13' height='21' viewBox='0 0 13 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.5332 2.28577L2.42741 8.91199C1.4485 9.71222 1.44849 11.2087 2.42741 12.0089L10.5332 18.6352' stroke='%23A7A7A7' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      background-size: 100% 100%; } } }

.slick-next.slick-disabled {
  &:hover, &:focus {
    &:before {
      background-image: url("data:image/svg+xml,%3Csvg width='12' height='21' viewBox='0 0 12 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.53955 2.28577L9.64534 8.91199C10.6243 9.71222 10.6243 11.2087 9.64535 12.0089L1.53955 18.6352' stroke='%23A7A7A7' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      background-size: 100% 100%; } } }

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto; }

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px; }

.slick-slide {
  outline: none; }

.slider-nav {
  position: relative;
  z-index: 1;
  margin: 20px 0;
  &_right {
    text-align: right; }
  .slick-prev, .slick-next {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    margin: 0 6px; }
  .slick-next {
    margin-right: 0; }
  .slick-prev {
    margin-left: 0; } }



/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slick-prev, .slick-next {
    width: $SL-nav-wh;
    height: $SL-nav-wh;
    margin: -($SL-nav-wh/2) 0 0;
    &:before {
      height: 22px;
      width: 12px;
      margin-top: -11px; } }

  /* end media min width 1000 pixels */ }
