/* ----- Portfolio component ------------------- */
.cap-section {
  text-align: center;
  margin-bottom: 40px;
  &__txt {
    color: $cl-4;
    font-size: 12px;
    line-height: 16px; } }

.subtt {
  color: $cl-8;
  font-family: $site-font-family, Arial, sans-serif;
  font-weight: 500; }

.g-box__half-col_alone {
  height: 220px; }

.g-box-img {
  display: block;
  text-decoration: none;
  outline: none;
  transition: all 0.2s linear;
  overflow: hidden;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  >img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.2s linear; } }


a.g-box-img {
  &:hover, &:focus {
    img {
      transform: scale(1.05); } } }

.g-box__half-col {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-bottom: 5px;
  > .half-col {
    height: 140px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    > .half-row {
      min-width: 49%;
      max-width: 49%; } } }

.i-box-tt {
  color: $cl-4;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 15px;
  font-weight: 500; }

.i-box__left {
  margin-bottom: 20px; }

.check-list {
  li {
    position: relative;
    padding-left: 23px;
    color: $cl-6;
    font-size: 14px;
    line-height: 20px;
    &:before {
      content: ' ';
      position: absolute;
      top: 5px;
      left: 0;
      width: 12px;
      height: 9px;
      background: url(../img/icons/check.svg) center center no-repeat; } } }

.p-slider-item__gallery {
  margin-bottom: 20px; }

.icons-box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
  &__icon {
    flex-shrink: 0;
    margin-right: 15px;
    min-width: 44px;
    text-align: center; }
  &-txt {
    flex-group: 1;
    &__name {
      color: $cl-4;
      font-size: 12px;
      font-weight: 500;
      line-height: 12px;
      margin-bottom: 5px; }
    &__value {
      color: $cl-6;
      font-size: 15px;
      line-height: 18px; } } }

.i-box-btn {
  margin-top: 20px; }

.btn_long {
  min-width: 260px; }

.icons-boxes {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  .icons-box {
    margin-right: 30px; } }

.bl-border {
  margin-left: -10px;
  margin-right: -10px;
  position: relative;
  z-index: 1;
  padding: 30px 10px;
  background: url(../img/border.png) center center no-repeat;
  background-size: auto 100%; }

.p-slider-nav.slider-nav {
  margin-bottom: 0; }

.bl-border {
  &_slider-no-cap {
    background: none;
    padding-top: 0; } }

.icons-boxes_wrapp {
  margin: 20px 0 0; }

.icons-box-txt__value_big {
  color: $cl-7;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px; }

.value-list {
  li {
    position: relative;
    z-index: 1;
    padding-left: 40px;
    color: $cl-6;
    font-size: 14px;
    line-height: 20px; }
  > * + * {
    margin-top: 15px; }
  &__value {
    position: absolute;
    top: -4px;
    left: 0;
    background: url(../img/icons/ellipse-value.svg) center center no-repeat;
    width: 30px;
    height: 28px;
    background-size: 100% 100%;
    text-align: center;
    line-height: 28px;
    color: $cl-10;
    font-size: 12px;
    font-weight: 700; } }

.i-box__txt {
  color: $cl-6;
  font-size: 14px;
  line-height: 20px; }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {

  .g-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__half-col {
      min-width: calc(50% - 5px);
      max-width: calc(50% - 5px);
      height: 350px;
      margin-bottom: 5px;
      > .half-col {
        min-height: calc(50% - 5px);
        max-height: calc(50% - 5px);
        margin: 0; } } }

  .i-box {
    display: flex;
    justify-content: space-between;
    &__left {
      min-width: 40%;
      max-width: 40%; }
    &__right {
      min-width: 57%;
      max-width: 57%; } }

  .icons-box-txt__value_big {
    font-size: 24px;
    line-height: 30px; }

  .value-list {
    li {
      font-size: 16px; } }

  .p-slider-item__cap-grid {
    padding-right: 250px; }

  .p-slider-wrapp_second {
    .p-slider-nav {
      position: absolute;
      top: 380px;
      right: 10px;
      margin: 0; } }

  .i-box_second {
    .i-box__left {
      min-width: 48%;
      max-width: 48%; }
    .i-box__right {
      min-width: 48%;
      max-width: 48%; } }

  .icons-boxes_wrapp {
    &.icons-boxes .icons-box {
      margin-right: 50px;
      &:last-child {
        margin-right: 0; } } }

  .icons-boxes .icons-box {
    &:last-child {
      margin-right: 0; } }

  .p-slider-item__cap-grid {
    min-height: 100px; }

  .i-box__txt {
    font-size: 16px;
    line-height: 22px; }

  .p-slider-wrapp_second {
    margin-top: 60px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .cap-section {
    &__txt {
      font-size: 16px;
      line-height: 24px; } }

  .i-box-tt {
    font-size: 21px;
    line-height: 26px;
    margin-bottom: 25px; }

  .check-list {
    li {
      font-size: 15px;
      line-height: 18px;
      margin-bottom: 10px; } }

  .icons-box {
    &-txt {
      &__value {
        color: $cl-6;
        font-size: 16px;
        line-height: 20px; } } }

  .bl-border {
    background-size: 100% 100%;
    margin: 0;
    padding: 40px; }

  .icons-box-txt__value_big {
    font-size: 30px;
    line-height: 40px; }

  .value-list {
    li {
      font-size: 16px;
      line-height: 24px; }
    &__value {
      top: -1px; } }

  .p-slider-wrapp_second .p-slider-nav {
    top: 430px;
    right: 40px; }

  .icons-box-txt__value_big {
    color: $cl-10; }

  .icons-box__icon_1 {
    width: 61px;
    height: 60px;
    img {
      width: 100%;
      height: 100%; } }

  .icons-box__icon_2 {
    width: 78px;
    height: 58px;
    img {
      width: 100%;
      height: 100%; } }

  .icons-box__icon_3 {
    width: 58px;
    height: 57px;
    img {
      width: 100%;
      height: 100%; } }

  .p-slider-item__cap-grid {
    .title-site, h1, h2, h3, h4, h5, h6 {
      margin-bottom: 35px; } }

  .value-list li {
    padding-left: 50px; }

  .value-list > * + * {
    margin-top: 20px; }

  .p-slider-item__cap-grid {
    padding-right: 350px; }

  .p-slider-wrapp_second .p-slider-nav.slider-nav {
    padding: 0; }

  .bl-border {
    &_slider-no-cap.bl-border {
      background: url(../img/border.png) center bottom no-repeat;
      background-size: 100% 100%;
      padding-top: 0; } }

  .p-slider-wrapp_second {
    margin-top: 0; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .g-box {
    &__half-col {
      min-width: calc(50% - 7px);
      max-width: calc(50% - 7px);
      height: 380px;
      margin-bottom: 7px;
      > .half-col {
        min-height: calc(50% - 7px);
        max-height: calc(50% - 7px);
        > .half-row {
          min-width: calc(50% - 7px);
          max-width: calc(50% - 7px); } } } }

  .p-slider-wrapp_second .p-slider-nav {
    top: 440px; }

  .icons-boxes_wrapp.icons-boxes .icons-box {
    margin-right: 78px; }
  /* end media min width 1200 pixels */ }


/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .p-slider-wrapp {
    margin-left: -120px;
    margin-right: -120px;
    .p-slider-item__info, .p-slider-nav {
      padding-left: 120px;
      padding-right: 120px; } }

  .bl-border {
    padding: 52px 40px 50px; }

  .cap-section_push-tt {
    .title-site_h1, .title-site_h2, h1, h2 {
      margin-bottom: 33px; } }

  .cap-section_push-big {
    margin-bottom: 52px; }

  .p-slider-item__gallery {
    margin-bottom: 40px; }

  .i-box {
    .icons-boxes {
      margin-bottom: 33px; }
    .icons-boxes .icons-box {
      margin-right: 25px; } }
  .i-box__left {
    padding-left: 30px;
    .check-list li {
      margin: 16px 0; } }

  .icons-box__icon_sm {
    max-width: 30px;
    min-width: 30px; }

  .p-slider-nav.slider-nav {
    margin-top: 0px; }

  .p-slider-item__gallery.g-box_second {
    margin-bottom: 40px; }

  .p-slider-wrapp_second .p-slider-nav {
    top: 445px; }

  .i-box_second {
    .i-box__left {
      padding-left: 0; } }

  .i-box_second {
    .i-box__right {
      min-width: 42%;
      max-width: 42%; } }

  .p-slider-wrapp_second .p-slider-nav.slider-nav {
    right: 90px; }

  /* end media min width 1350 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .p-slider-wrapp {
    margin-left: -140px;
    margin-right: -140px;
    .p-slider-item__info, .p-slider-nav {
      padding-left: 140px;
      padding-right: 140px; } }
  .g-box_second {
    .g-box__half-col {
      height: 430px; } }

  .p-slider-wrapp_second .p-slider-nav {
    top: 490px; }

  .p-slider-wrapp_second .p-slider-item__info, .p-slider-wrapp_second .p-slider-nav {
    padding-left: 160px;
    padding-right: 160px; }
  /* end media min width 1440 pixels */ }

/* ----- END Portfolio component --------------- */
