/* ----- Header ------------------------ */
.header {
  position: relative;
  padding: 10px 0; }

.logo {
  &_header {
    max-width: 170px; }
  a {
    display: block;
    outline: none; } }

.header-inner__right {
  display: none; }

.header-inner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-height: 65px;
  padding-right: 50px; }

a.icon-point, button.icon-point {
  text-decoration: none;
  outline: none;
  &:hover, &:focus {
    text-decoration: none;
    color: $cl-6;
    .icon-point__txt {
      text-decoration: underline; } } }



.icon-point {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: $cl-6;
  font-size: 12px;
  line-height: 16px;
  &__icon {
    min-width: 18px;
    position: relative; } }
.icon-point_top {
  .icon-point__icon {
    top: -2px; } }

.head-btn {
  .btn.btn_min-width {
    font-size: 12px;
    min-width: 180px; } }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .head-adr>* + * {
    margin-top: 10px; }
  .icon-point__txt {
    b {
      font-size: 16px; } }

  .header-inner {
    justify-content: space-between;
    align-items: center;
    flex-direction: row; }

  .header-inner__right {
    display: block; }
  .header-inner__right {
    .head-btn {
      display: none; } }

  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .header-inner__right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .head-btn {
      display: block;
      margin-left: 20px; } }
  .icon-point__txt {
    b {
      font-size: 18px; } }

  .icon-point {
    font-size: 16px;
    line-height: 20px; }

  .logo_header {
    max-width: 190px; }
  .header-inner__left {
    margin-right: 20px; }

  .header_push {
    padding: 20px 0;
    .burger-menu {
      top: 33px; } }
  /* end media min width 768 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .header-inner {
    padding-right: 0; }

  .icon-point__txt {
    b {
      font-size: 16px; } }

  .icon-point {
    font-size: 14px;
    line-height: 20px; }

  .logo_header {
    max-width: 150px; }

  .head-adr > * + * {
    margin-top: 6px; }

  .header .container {
    max-width: (1660*0.8)+px; }
  /* end media min width 1200 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .icon-point__txt {
    b {
      font-size: 16px; } }

  .icon-point {
    font-size: 14px;
    line-height: 20px; }

  .head-btn {
    .btn {
      min-width: 210px; } }

  .header-inner__right .head-btn {
    margin-left: 40px; }

  .header_push {
    padding: 30px 0; }
  /* end media min width 1440 pixels */ }

/* Min width 1660 pixels */
@media only screen and (min-width: 1660px) {
  .logo_header {
    max-width: 180px; }

  .header_push {
    padding: 30px 0; }

  .header-inner__right .head-btn {
    margin-left: 40px; }
  /* end media min width 1660 pixels */ }

/* ----- END Header -------------------- */
