/* ----- slider img scroll ------------------- */
.ba-slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  &__inner {
    width: 100%;
    height: 100%;
    margin: 15px 0;
    position: relative;
    height: 300px;
    border-radius: 10px;
    overflow: hidden; } }


.ba-slider img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  max-width: none; }

.resize {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  overflow: hidden; }

.handle {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 5px;
  border-radius: 5px;
  background-color: $cl-10;
  margin-left: -2px;
  cursor: ew-resize; }

.handle:after {
  position: absolute;
  top: 50%;
  width: 46px;
  height: 46px;
  margin: -23px 0 0 -20px;
  content: "";
  background: #fff;
  border: 12px solid $cl-10;
  border-radius: 50%;
  transition: all 0.3s ease;
  box-sizing: border-box; }

.draggabl:after {
  width: 36px;
  height: 36px;
  margin: -18px 0 0 -15px;
  border-width: 10px; }

.handle:before {
  content: ' ';
  position: absolute;
  top: 15px;
  bottom: 15px;
  right: 100%;
  width: 50px;
  border-radius: 10px 0 0 10px;
  background-image: linear-gradient(to left, #abedc6 0%, rgba(142, 211, 169, 0.62) 0%, rgba(142, 211, 169, 0) 100%); }

// NEXT sstyle

.ba-slider-wrapp {
  position: relative;
  padding-bottom: 20px; }

.ba-slider__txt {
  border-radius: 28px;
  background-color: $cl-3;
  color: $cl-27;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  padding: 6px 15px;
  position: absolute;
  bottom: 14px;
  &_from {
    left: 10px; }
  &_to {
    right: 10px; } }

.slider-one {
  .slick-prev, .slick-next {
    top: 136px;
    margin-top: 0; } }

.slider-one-wrapp {
  padding-bottom: 60px; }

.info-box__name {
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  margin-bottom: 5px;
  color: $cl-4; }
.info-box__tt {
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  color: $cl-6;
  margin-bottom: 5px; }
.info-box__adr {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: $cl-6; }

.info-box {
  p {
    color: $cl-4;
    font-size: 14px;
    line-height: 18px; } }

.info-box__left {
  margin-bottom: 20px; }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .ba-slider__txt {
    font-size: 24px;
    padding: 6px 20px;
    bottom: 18px; }
  .info-box__name {
    font-size: 14px;
    line-height: 21px; }
  .info-box__tt {
    font-size: 24px;
    line-height: 36px; }
  .info-box__adr {
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 0; }
  .info-box {
    p {
      font-size: 16px;
      line-height: 22px; } }

  .info-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 970px;
    margin: 20px auto 40px;
    &__left {
      flex-shrink: 0;
      min-width: 40%;
      max-width: 40%;
      padding-right: 25px; }
    &__right {
      flex-shrink: 0;
      min-width: 60%;
      max-width: 60%;
      > * {
        margin-bottom: 0; }
      > * + * {
        margin-top: 10px; } } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .info-box {
    p {
      font-size: 18px;
      line-height: 30px; } }

  .ba-slider__inner {
    height: 430px; }

  .slider-one .slick-prev, .slider-one .slick-next {
    top: 200px; }
  .ba-slider__txt_from {
    left: 30px; }
  .ba-slider__txt_to {
    right: 30px; }

  .info-box {
    margin-bottom: 80px; }
  /* end media min width 1000 pixels */ }


/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .slider-one .slick-prev {
    left: -70px; }
  .slider-one .slick-next {
    right: -70px; }
  /* end media min width 1350 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .slider-one .slick-prev {
    left: -120px; }
  .slider-one .slick-next {
    right: -120px; }
  /* end media min width 1440 pixels */ }

/* ----- END slider img scroll --------------- */
