/* ----- about ------------------- */

.about-bg {
  background: url(../img/about-bg.png) left -267px center no-repeat;
  background-size: auto 100%;
  padding: 70px 0 70px;
  color: #fff;

  &_second {
    background-image: url(../img/about-bg2.png); }
  .title-site, h1, h2, h3, h4, h5, h6 {
    color: #fff; } }

.about-content {
  max-width: 740px;
  .title-site_h2, h2 {
    margin-bottom: 10px; }
  .title-site_h3, h3 {
    font-family: $site-font-family, Arial, sans-serif;
    font-weight: 500;
    margin-bottom: 30px; } }

.about-bg_second.about-bg_second_wrapp-bg_center {
  background-position: center center;
  background-size: auto 143%;
  padding-bottom: 100px;
  &.about-bg_second_wrapp-bg_center_second {
    background-size: auto 130%; } }

.about-bg_second.about-bg_second_wrapp-bg_center_three {
  background-position: center center;
  background-size: auto 106%;
  padding-bottom: 100px; }

.about-bg.about-bg_three {
  background-size: auto 139%; }

.about-bg.about-bg_center-st {
  padding: 100px 0 150px;
  background-position: center center; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .about-bg {
    background-position: left -167px center;
    padding: 70px 0 120px; }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .about-bg_second.about-bg_second_wrapp-bg_center {
    padding: 100px 0 150px;
    &.about-bg_second_wrapp-bg_center_second {
      min-height: 560px;
      .about-content {
        p {
          max-width: 400px; } } } }

  .about-bg_second.about-bg_second_wrapp-bg_center_three {
    padding: 100px 0 150px;
    .about-content {
      p {
        max-width: 400px; } } }

  .about-bg.about-bg_center-st {
    padding: 100px 0 150px;
    background-position: center center;
    .about-content {
      p {
        max-width: 400px; } } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .about-bg {
    &_second {
      padding: 120px 0 170px; } }
  .about-bg_second.about-bg_second_wrapp-bg_center {
    padding: 130px 0 160px; }

  .about-bg_second.about-bg_second_wrapp-bg_center_three {
    padding: 130px 0 220px;
    .about-content {
      p {
        max-width: 650px; } } }

  .about-bg.about-bg_three {
    min-height: 540px;
    background-position: left calc(50% - 50px) center;
    padding: 120px 0 150px;
    .about-content {
      p {
        max-width: 500px; } } }

  .about-bg.about-bg_center-st {
    padding: 140px 0 280px;
    .about-content {
      p {
        max-width: 650px; } } }
  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .about-bg {
    margin-top: 40px;
    padding: 130px 0 150px;
    background-position: left calc(50% + 140px) center; }
  .about-content {
    p {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px; } }

  .about-content .title-site_h3, .about-content h3 {
    margin-bottom: 40px; }

  .about-bg {
    &_second {
      padding: 180px 0 150px;
      min-height: 770px;
      background-position: left calc(50% - 20px) center;
      .about-content .title-site_h2, .about-content h2 {
        margin-bottom: 40px; }
      .about-content p {
        margin-bottom: 60px;
        font-size: 18px;
        line-height: 30px; } } }

  .about-bg_second_wrapp3 {
    padding-top: 210px; }

  .about-bg_second.about-bg_second_wrapp4 {
    padding-top: 210px;
    margin-top: 0; }

  .about-bg_second.about-bg_second_wrapp-bg {
    background-position: left calc(50% + 210px) center; }

  .about-bg_second.about-bg_second_wrapp-bg_center {
    background-position: left calc(50% + 40px) center;
    padding: 130px 0 190px;
    min-height: 640px; }

  .about-bg_second.about-bg_second_wrapp-bg_center {
    &.about-bg_second_wrapp-bg_center_second {
      min-height: 740px;
      padding-top: 140px;
      margin-top: 0;
      .about-content {
        p {
          max-width: 650px; } } } }

  .about-bg_second.about-bg_second_wrapp-bg_center_three {
    padding: 210px 0 220px;
    min-height: 770px;
    background-position: left calc(50% + 80px) center; }

  .about-bg.about-bg_three {
    .about-content {
      p {
        max-width: 650px; } } }

  .about-bg.about-bg_center-st {
    padding: 140px 0 234px;
    min-height: 600px; }
  /* end media min width 1000 pixels */ }


/* Min width 1660 pixels */
@media only screen and (min-width: 1660px) {
  .about-bg {
    &_second {
      background-position: right center; } }

  .about-bg.about-bg_three {
    min-height: 590px;
    background-position: left center;
    padding: 150px 0 150px; }
  /* end media min width 1660 pixels */ }


/* Min width 1900 pixels */
@media only screen and (min-width: 1900px) {
  .about-bg.about-bg_three {
    min-height: 700px;
    background-position: left center;
    padding: 180px 0 150px;
    .about-content > p {
      font-size: 18px;
      line-height: 30px;
      margin-bottom: 40px; }
    .about-content > * + * {
      margin-top: 30px; } }


  /* end media min width 1900 pixels */ }


/* Min width 2200 pixels */
@media only screen and (min-width: 2200px) {
  .about-bg {
    &_second {
      background-size: 83% auto; } }


  .about-bg.about-bg_three {
    background-size: 100% 139%; }
  /* end media min width 2200 pixels */ }

/* Min width 2500 pixels */
@media only screen and (min-width: 2500px) {
  .about-bg {
    &_second {
      background-size: 79% auto; } }

  .about-bg_second.about-bg_second_wrapp-bg {
    background-position: right center; }
  /* end media min width 2500 pixels */ }

/* ----- END about --------------- */
