/* ----- Step Works ------------------- */
.step-list__item {
  position: relative;
  z-index: 1; }
.s-img {
  max-width: 110px;
  position: absolute;
  z-index: -1;
  top: -15px;
  left: -15px; }

.step-info {
  display: flex;
  justify-content: flex-start;
  padding-left: 50px;
  min-height: 100px;
  margin-bottom: 40px;
  &__value {
    flex-shrink: 0;
    margin-right: 10px;
    color: $cl-6;
    font-family: Montserrat;
    font-size: 40px;
    line-height: 1;
    font-weight: 100; }
  &__txt {
    color: $cl-6;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    b {
      font-weight: 700; }
    p {
      margin-bottom: 5px; }
    > span {
      font-size: 12px;
      line-height: 14px;
      color: $cl-4; } } }

.step-list__item {
  &:last-child {
    .step-info {
      margin-bottom: 0; } } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .step-info {
    &__value {
      font-size: 72px; }
    &__txt {
      font-size: 18px;
      line-height: 30px;
      > span {
        font-size: 14px;
        line-height: 16px; } } }

  .step-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 80px 0;
    &__item {
      min-width: 49%;
      max-width: 49%; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .step-list {
    display: block;
    margin: 30px 0 0;
    position: relative;
    z-index: 1;
    padding: 73px 0; }

  .s-img {
    max-width: none;
    top: -51px; }

  .step-list__item {
    min-width: 1px;
    max-width: none;
    width: auto;
    &:nth-child(1) {
      margin-left: 4px;
      margin-bottom: 40px; }
    &:nth-child(2) {
      margin-left: 300px;
      margin-bottom: 50px;
      .step-info__txt {
        padding-top: 7px; }
      .s-img {
        top: -64px;
        left: -3px; } }
    &:nth-child(3) {
      margin-left: 100px;
      margin-bottom: 80px;
      .s-img {
        top: -94px;
        left: -23px; } }
    &:nth-child(4) {
      margin-left: 320px;
      margin-bottom: 91px;
      .s-img {
        top: -82px;
        left: 27px; } }
    &:nth-child(5) {
      margin-left: 225px;
      .s-img {
        top: -77px;
        left: 27px; } } }
  .step-info {
    padding-left: 169px;
    margin-bottom: 18px; }

  .step-info__value {
    margin-right: 22px; }

  .section-st_step-works {
    .container {
      max-width: 1170px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .step-list {
    background: url(../img/bg-list-line.jpg) left 130px top no-repeat; }
  .step-list__item {
    &:nth-child(1) {
      margin-left: 4px;
      margin-bottom: 20px; }
    &:nth-child(2) {
      margin-left: 566px;
      margin-bottom: 157px;
      .step-info__txt {
        padding-top: 7px; }
      .s-img {
        top: -64px;
        left: -3px; } }
    &:nth-child(3) {
      margin-left: 100px;
      margin-bottom: 40px;
      .s-img {
        top: -94px;
        left: -23px; } }
    &:nth-child(4) {
      margin-left: 669px;
      margin-bottom: 91px;
      .s-img {
        top: -82px;
        left: 27px; } }
    &:nth-child(5) {
      margin-left: 225px;
      .s-img {
        top: -77px;
        left: 27px; } } }
  /* end media min width 1200 pixels */ }

/* ----- END Step Works --------------- */
